.genre-group:hover {
}

.data-bar {
  fill: rgba(167, 167, 167, 0.8);
  /* stroke-alignment: outside; */
}
.empty-bar {
  fill: rgba(167, 167, 167, 0.3);
}
/* 
.data-bar:hover {
  pointer-events: bounding-box;
  fill: coral;
} */

.genre-label {
  font-size: 11px;
  fill: rgb(50, 50, 50);
  text-anchor: end;
}

svg {
  font-family: Helvetica, Arial, sans-serif;
  user-select: none;
}

a {
  color: inherit;
  text-decoration: inherit;
  /* margin-bottom: 20px; */
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(15, 15, 15, 0.75);
  z-index: 10;
}

.modal-content {
  outline: none;
  position: absolute;
  user-select: none;
  height: 500px;
  background-color: rgb(209, 209, 209);
  border-radius: 1%;
  padding: 0px 20px 0px 20px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
}

.close-btn {
  fill: black;
  position: absolute;
  width: 20px;
  cursor: pointer;
  top: 0;
  right: 0;
  margin: 15px 15px 0px 0px;
  transition: all 0.2s ease;
}

.close-btn:hover {
  fill: #6d76af;
  transition: all 0.2s ease;
}

#modal-footer {
  grid-row: 3;
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
}

#songkick-logo {
  height: 25px;
  fill: grey;
  transition: fill 0.4s;
}

#songkick-logo:hover {
  fill: #6d76af;
}

.modal-container {
  /* margin: 20px 20px 20px 20px; */
  display: grid;
  grid-template-rows: 80px auto 40px;
  height: inherit;
  width: 80vw;
  max-width: 500px;
}

.modal-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-row: 1;
  height: 80px;
  margin-left: 10px;
  position: fixed;
}

.genre-name {
  font-weight: 900;
  font-size: 24px;
  display: inline;
}

.city-name {
  font-weight: 900;
  font-size: 18px;
  display: inline;
  color: grey;
}

.date-range {
  font-size: 14px;
}

.event-cont {
  overflow-y: scroll;
  grid-row: 2;
  display: flex;
  flex-direction: column;
  height: auto;
}

.event-box {
  padding: 10px 10px 10px 10px;
  margin-bottom: 20px;
  background-color: white;
  border-radius: 3px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.event-box:hover {
  background-color: #6d76af41;
  transition: all 0.2s ease;
}

.event-header {
  display: flex;
  font-size: 12px;
  margin-bottom: 8px;
  justify-content: space-between;
}
/* 
.event-date {
} */

.event-genres {
  font-style: italic;
  color: grey;
}

.event-name {
  font-weight: 800;
  font-size: 16px;
}

.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 200ms ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}
